.main {
    height: 100vh;
    background: url('./images/background.png'), radial-gradient(69.07% 69.07% at 51.54% 24.26%, #56456B 0.01%, #070707 100%);
    background-size: cover;
    padding: 100px 100px 0 100px;
    overflow: hidden;
  }
  
  .logo {
    background: url('./images/logo.png');
    background-size: cover;
    width: 192px;
    height: 37px;
    margin-bottom: 50px;
  }
  
  .header {
    margin-top: 40px;
    margin-bottom: 80px;
  }
  
  .title {
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 300;
    font-size: 65px;
    line-height: 85px;
    letter-spacing: 0.06em;
    color: white;
  }
  
  .content {
    height: 100%;
    overflow: auto;
    padding-bottom: 100px;
  }